.textarea{
    width: 100%;
    padding: 15px;
    border: 0;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.05);
}

.submitButton{
    border: none;
    text-transform: uppercase;
    display: inline-block;
    padding: 12px 55px;
    background-color: #6bb7be;
    color: #ffffff;
    border-radius: 0px;
    border: 1px solid #6bb7be;
    -webkit-transition: all .2s;
    transition: all .2s;
    color: #fff !important;
    margin-top: 15px !important;
    width: 300px !important;
    cursor: pointer;
}